.NoteView {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    width: 42px;
}

.NoteView .note-area {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    min-width: 30px;
}

.NoteView .fret {
    float: right;
    background: #ddd;
    overflow: hidden;
    width: 3px;
    height: 100%;;
    margin-right: 0;
    -webkit-box-shadow: #444 0px 0px 2px;
            box-shadow: #444 0px 0px 2px;
}

.NoteView .string {
    background: grey;
    height: 2px;
    width: calc(100% + 3px);
    position: relative;
    top: calc(50% - 1px);
    float: left;
    z-index: 2;
    -webkit-box-shadow: #444 1px 1px 5px;
            box-shadow: #444 1px 1px 5px;
}

.NoteView .highlight {
    background: rgb(128, 228, 145) !important;
}

.NoteView .note {
    font-size: 10pt;
    background: white;
    width: 24px;   
    height: 24px;
    border-radius: 50%;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 3;
    margin: auto;
    -webkit-box-shadow: #444 1px 1px 3px;
            box-shadow: #444 1px 1px 3px;
}

.NoteView .note:hover { 
    background: #ddd;
    -webkit-box-shadow: #777 0px 0px 2px;
            box-shadow: #777 0px 0px 2px;
}



